// import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
    {
        todoID: 0,
        todoText: "Finish Homework",
        todoComplete: false
    },
    {
        todoID: 1,
        todoText: "Walk the dog",
        todoComplete: true
    },
    {
        todoID: 2,
        todoText: "Clean my room",
        todoComplete: false
    }
];

function createDoneItem(text, id) {
    let list = document.getElementById('todoList')
    let line = document.createElement('li')
    line.className = 'done'
    line.innerText = text
    line.dataset.id = id
    list.append(line)
}

function createListItem(text, id) {
    let list = document.getElementById('todoList')
    let line = document.createElement('li')
    line.innerText = text
    line.dataset.id = id

    let edit = document.createElement('span')
    edit.className = 'editBtn'

    let editIcon = document.createElement('i')
    editIcon.className = 'fa fa-edit'
    edit.append(editIcon)
    line.append(edit)

    let trash = document.createElement('span')
    let trashIcon = document.createElement('i')
    trashIcon.className = 'fa fa-trash'
    trash.append(trashIcon)
    line.append(trash)


    list.append(line)
}

function deleteList() {
    let list = document.getElementById('todoList')
    while (list.firstChild) {
        list.removeChild(list.firstChild)
    }
}

function refreshList() {
    // let list = document.getElementById('todoList')
    deleteList()
    for (let item in todos) {
        if (todos[item].todoComplete === true) {
            createDoneItem(todos[item].todoText, todos[item].todoID)
        } else {
            createListItem(todos[item].todoText, todos[item].todoID)
        }
    }

    let doList = document.querySelector('#todoList').querySelectorAll("li")
    doList.forEach(item => {
        item.addEventListener('click', ()=> {
            toggleTodo(item.dataset.id)
        })
    })

    let trash = document.querySelectorAll('.fa-trash')
    trash.forEach(icon => {
        icon.addEventListener('click', ()=> {
            // this doesn't work, would do this different
            todos[icon.parentElement.parentElement.dataset.id].pop()
        })
    })
}

function addItem() {
    let todo = {
        todoID: todos.length + 1,
        todoText: document.getElementById('todoItem').value,
        todoComplete: false
    }
    todos.push(todo)
    refreshList()
}

function toggleTodo(id) {
    todos[id].todoComplete = !todos[id].todoComplete
    refreshList()
}

document.getElementById('todoItem').addEventListener('keypress', (e) => {
    if (e.key === 'Enter') {
        addItem()
    }
})

document.getElementById('addButton').addEventListener('click', () => {
    addItem()
})




refreshList()
